<template>
    <div class="login">
        <span class="iconfont icon-xitongfanhui" @click="$router.go(-1);store.state.nowLogin=false"></span>
        <div id="login_box">
            <h2>LOGIN</h2>
            <input type="text" placeholder="请输入手机号" v-model.number="loginInfo.phone">
            <br>
            <input type="password" placeholder="请输入密码" v-model="loginInfo.password" autocomplete="off">
            <br>
            <button @click="loginClick()">登录</button><br>
        </div>
    </div>
</template>
<script>
import { watch,getCurrentInstance } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
export default{
    name:'login',
    setup(){
        let store =useStore()
        let route=useRouter()
        let {proxy}=getCurrentInstance()
        let loginInfo={
            phone:'',
            password:'',
        };
        //提交信息至vuex
        let loginClick=()=>{
            store.dispatch('reqUserInfo',loginInfo)
        };
        //观察islogin变量，决定报告登录失败还是跳转至个人中心
        watch(
            ()=>store.state.isLogin,
            ()=>{
                if(store.state.isLogin){
                    route.push('/my');
                }else{
                    alert('账号或密码错误')
                }
            },
        );
        return{
            store,loginInfo,loginClick,route,proxy
        }
    }
}
</script>
